"use client"

import { Bar, BarChart, ResponsiveContainer, XAxis, YAxis, Tooltip } from "recharts"
import { DollarSign, Package, ShoppingBag, Users } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { StatCard } from "@/components/features/seller/StatCard"
import { StatCardSkeleton, ChartSkeleton } from "@/components/ui/skeleton"
import { ErrorState } from "@/components/ErrorBoundary"
import { useSellerStats, mockSellerStats } from "@/lib/hooks/queries/useSellerStats"
import { formatPrice } from "@/lib/utils"

export default function SellerDashboardPage() {
    const { data, isLoading, isError, error, refetch } = useSellerStats()

    // Use actual data if available, fallback to mock data
    const stats = data || mockSellerStats
    const usesMockData = !data

    if (isError) {
        return (
            <div className="space-y-8">
                <div>
                    <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>
                    <p className="text-muted-foreground">
                        Overview of your store's performance
                    </p>
                </div>
                <ErrorState
                    title="Failed to load dashboard"
                    message={error?.message || "Unable to fetch dashboard data"}
                    onRetry={() => refetch()}
                />
            </div>
        )
    }

    return (
        <div className="space-y-8">
            <div>
                <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>
                <p className="text-muted-foreground">
                    Overview of your store's performance
                    {usesMockData && (
                        <span className="ml-2 text-xs text-yellow-600 dark:text-yellow-400">
                            (Using mock data - backend not connected)
                        </span>
                    )}
                </p>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                {isLoading ? (
                    <>
                        <StatCardSkeleton />
                        <StatCardSkeleton />
                        <StatCardSkeleton />
                        <StatCardSkeleton />
                    </>
                ) : (
                    <>
                        <StatCard
                            title="Total Revenue"
                            value={formatPrice(stats.totalRevenue)}
                            icon={DollarSign}
                            trend={{ value: stats.revenueChange, label: "from last month" }}
                            description="from last month"
                        />
                        <StatCard
                            title="Orders"
                            value={`+${stats.totalOrders}`}
                            icon={ShoppingBag}
                            trend={{ value: stats.ordersChange, label: "from last month" }}
                            description="from last month"
                        />
                        <StatCard
                            title="Products"
                            value={stats.totalProducts.toString()}
                            icon={Package}
                            description="Active listings"
                        />
                        <StatCard
                            title="Active Customers"
                            value={`+${stats.activeCustomers}`}
                            icon={Users}
                            description="Total customers"
                        />
                    </>
                )}
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
                <Card className="col-span-4">
                    <CardHeader>
                        <CardTitle>Revenue Overview</CardTitle>
                    </CardHeader>
                    <CardContent className="pl-2">
                        {isLoading ? (
                            <ChartSkeleton />
                        ) : (
                            <ResponsiveContainer width="100%" height={350}>
                                <BarChart data={stats.monthlyRevenue}>
                                    <XAxis
                                        dataKey="month"
                                        stroke="#888888"
                                        fontSize={12}
                                        tickLine={false}
                                        axisLine={false}
                                    />
                                    <YAxis
                                        stroke="#888888"
                                        fontSize={12}
                                        tickLine={false}
                                        axisLine={false}
                                        tickFormatter={(value) => `$${value}`}
                                    />
                                    <Tooltip
                                        cursor={{ fill: 'transparent' }}
                                        contentStyle={{
                                            borderRadius: '8px',
                                            border: 'none',
                                            boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
                                        }}
                                    />
                                    <Bar dataKey="total" fill="#16a34a" radius={[4, 4, 0, 0]} />
                                </BarChart>
                            </ResponsiveContainer>
                        )}
                    </CardContent>
                </Card>

                <Card className="col-span-3">
                    <CardHeader>
                        <CardTitle>Recent Sales</CardTitle>
                        <p className="text-sm text-muted-foreground">
                            {isLoading ? "Loading..." : `You made ${stats.recentSales.length} recent sales.`}
                        </p>
                    </CardHeader>
                    <CardContent>
                        {isLoading ? (
                            <div className="space-y-4">
                                {Array.from({ length: 3 }).map((_, i) => (
                                    <div key={i} className="flex items-center gap-4">
                                        <div className="h-9 w-9 rounded-full bg-muted animate-pulse" />
                                        <div className="flex-1 space-y-2">
                                            <div className="h-4 w-32 bg-muted rounded animate-pulse" />
                                            <div className="h-3 w-48 bg-muted rounded animate-pulse" />
                                        </div>
                                        <div className="h-4 w-20 bg-muted rounded animate-pulse" />
                                    </div>
                                ))}
                            </div>
                        ) : (
                            <div className="space-y-8">
                                {stats.recentSales.map((sale, index) => {
                                    const initials = sale.customerName
                                        .split(' ')
                                        .map(n => n[0])
                                        .join('')
                                        .toUpperCase()

                                    const colors = [
                                        'bg-green-100 text-green-700',
                                        'bg-blue-100 text-blue-700',
                                        'bg-yellow-100 text-yellow-700',
                                        'bg-purple-100 text-purple-700',
                                        'bg-pink-100 text-pink-700',
                                    ]
                                    const colorClass = colors[index % colors.length]

                                    return (
                                        <div key={sale.id} className="flex items-center">
                                            <div className={`h-9 w-9 rounded-full ${colorClass} flex items-center justify-center font-bold text-sm`}>
                                                {initials}
                                            </div>
                                            <div className="ml-4 space-y-1">
                                                <p className="text-sm font-medium leading-none">
                                                    {sale.customerName}
                                                </p>
                                                <p className="text-sm text-muted-foreground">
                                                    {sale.customerEmail}
                                                </p>
                                            </div>
                                            <div className="ml-auto font-medium">
                                                +{formatPrice(sale.amount)}
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>
                        )}
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
